

nav {
    height: 80px;
    background-color: rgb(51, 216, 19);
}

h2 a {
    color: white;
}

h2 a:hover {
    text-decoration: none;
    color: white;
}
.jumbotron {
    background-image: url("https://image1.redbull.com/rbcom/010/2015-12-27/1331767414211_2/0010/1/1500/1000/2/star-wars-spaceships-death-star.jpg");
    background-repeat: no-repeat;
    background-position: center;
}

h1, p {
    color: white;
}
.card {
    height: 175px;
    width: 175px;
    border-radius: 2px;
    margin: 1rem;
    border: 5px solid white;
    box-shadow: 0 5px 5px #999, 0 3px 6px #999;
}

.card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: all .05s ease-in-out;
    transition: all .05s ease-in-out;
}
footer {
    background-color: rgb(51, 216, 19);
}

.footer-img {
    height: 30px;
    padding-left: 10px;
}

p {
    font-weight: bold;
}
